$block-bg: #FFFFFF;
$radius: 4px;

// color
$border-color: #e8e8e8;
// border
$border: 1px solid $border-color;

@mixin blank_page {
    background: $block-bg;
    padding: 16px;
    border: $radius;
}
.template-section {
  margin-bottom: 8px;

  &_selected {
    border: 2px solid #1890ff;
  }

  &_actions {
    font-size: 16px;
    cursor: pointer;

    :global {
      .anticon {
        margin-left: 8px;
      }

      .anticon:hover {
        color: #1890ff;
      }
    }
  }
}
