$block-bg: #FFFFFF;
$radius: 4px;

// color
$border-color: #e8e8e8;
// border
$border: 1px solid $border-color;

@mixin blank_page {
    background: $block-bg;
    padding: 16px;
    border: $radius;
}
.drag-form {
  // :global {
  // .ant-form-item {
  //     border: 2px dashed #1890ff;
  // }
  // }
}

.column {
  position: relative;

  .cloumn-setting {
    display: none;
  }

  &:hover {
    border: 1px dashed #1890ff;

    .cloumn-setting {
      position: absolute;
      top: 0;
      right: 4px;
      z-index: 10;
      display: block;
      cursor: pointer;
    }
  }
}

.drop-over {
  border: 1px dashed #1890ff;
}
