$block-bg: #FFFFFF;
$radius: 4px;

// color
$border-color: #e8e8e8;
// border
$border: 1px solid $border-color;

@mixin blank_page {
    background: $block-bg;
    padding: 16px;
    border: $radius;
}
.settings {
  margin-top: 12px;

  .field-item {
    display: flex;
    line-height: 36px;

    &-title {
      margin-right: 8px;
    }

    // &-body {
