$block-bg: #FFFFFF;
$radius: 4px;

// color
$border-color: #e8e8e8;
// border
$border: 1px solid $border-color;

@mixin blank_page {
    background: $block-bg;
    padding: 16px;
    border: $radius;
}
.template-container {
  // display: flex;
  // flex: 1;
  // flex-direction: column;
  .template-content {
    flex: 1;
  }
}

.template-footer {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  margin-top: 8px;
  padding: 8px;
  background-color: #fff;
}
